.cart_item {
    border: 1px solid black;
    padding: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.cart_item img {
    width: 200px;
    height: 150px;
    object-fit: contain;
}

.cart_item button {
    padding: 10px 15px;
    cursor: pointer;
}

.cart_item span {
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
}