.product {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 600px;
    margin: 30px auto;
    gap: 10px;
}

.product p {
    text-align: center;
}

.category {
    background-color: orange;
    padding: 10px;
    color: white;
    font-weight: 600;
    cursor: pointer;
    border-radius: 10px;
}

.add_btn {
    background-color: green;
    padding: 10px;
    color: white;
    font-weight: 600;
    cursor: pointer;
    border-radius: 10px;
}

.product a {
    text-decoration: none;
}

